/* Navbar.css */
.Navbar-cons{
width: 100%;
background-color: white;
}

main{
  font-family: 'Merriweather', serif;
  display: grid;
   place-items: center;
   background-color: white;
   width: 100%;
}

/* Navbar.css */

header {
  padding: 14px 14px;
  position: fixed;
  top: 30px;
  width: 100%;
  background: white;
  display: grid;
  z-index: 12;
  place-items: center;
  transition: top 0.3s ease;
}

.Navbar-con.scrolled {
  top: 0;
  transition: top 0.3s ease;
}

.navbar.scrolled {
  top: 0;
  transition: top 0.3s ease;
}

.navbar {
    display: flex;
      justify-content: space-between;
      align-items: center;
      width: 100%;
      padding-left: 15px;
      padding-right: 15px;
      max-width: 100rem;
      transition: top 0.3s ease;
}



  .links{
    display: flex;
      justify-content: center;
      align-items: center;
      list-style: none;
      gap: 40px;
      text-align: center;
      margin-right: 10rem;  }
  
  ul {
    display: block;
  }

  
  li {
    cursor: pointer;
    font-size: 13px;
    margin-left: 3em;
    margin-right: 3em;
    transition: 1sm linear;
  }

  li a{
    color: black;
    text-decoration: none;
    font-size: 16px;
    font-family: 'Playfair Display', sans-serif;
  }


  li:hover{
    color:  #d2b7b7 ;
  }


  .cart-container{
    position: relative;
    left: -15px;

  }
  
  .cart {
        list-style: none;
          width: 0px;
          font-size: 20px;
          margin: 0;
  }

  .cart:hover{
    color: black;
  }

  .cart-container span{
          position: absolute;
            top: -9px;
            right: -6px;
            font-size: 11px;
   }
  

   .cart-container img{
    width: 25px;
    height: 25px;
   }
  .logo {
    font-size: 1.4em;
    font-weight: bold;
    letter-spacing: 1px;
  }

  .logo h1{
    font-family: 'Sevillana', serif;
    font-weight: 700;
    letter-spacing: initial;
    font-size: 30px;
  }
  
  .dropdowncart{
    display: flex;
    align-items: center;
    gap: 10px;
  }

  .dropdown {
    display: none;
  }

  

  .dropdown button{
    background: none;
    border: none;
    vertical-align: inherit;
    cursor: pointer;
    font-size: 20px;
    }

                
.dropdownmenu-container {
  background-color: rgba(255, 255, 255, 0);
  z-index: 200;
  position: fixed;
  background-color: white;
  cursor: pointer;
  top: 0;
  left: 0;
  transition: disappear 2s ease-in-out;
  bottom: 0;
  right: 0;
  display: none;
}

.dropdownmenu-container.appear{
  display: block;
}





.menu-div ol {
  display: flex;
  flex-direction: column;
}

.menu-div ol li {
  display: flex;
  flex-direction: column;
  padding-top: 30px;
  list-style: none;
  padding-right: 25px;
}

.menu-div ol li span {
  border-bottom-style: solid;
  border-bottom-color: rgba(0, 0, 0, 0.222);
  border-bottom-width: 0.5px;
  padding-top: 10px;
}

.menu-div a {
  font-size: 13px;
  text-decoration: none;
  font-weight: 500;
  color: black;
  font-family: 'Playfair Display', sans-serif;
  letter-spacing: 0.5px;

}

.menu-div .talktosalebtn {
  padding-left: 2.2rem;
  padding-right: 2.2rem;
}


.wigstationlogo {
  display: flex;
  align-items: center;
  justify-content: space-around;
  margin-bottom: 1.0rem;
}

.wigstationlogo .Cancelmenu {
  margin-right: 2rem;
}

.wigstationlogo .Cancelmenu button {
  background: none;
  border: none;
  vertical-align: inherit;
  cursor: pointer;
  font-size: 20px;
}

.wigstationlogo div {
  display: flex;
  align-items: center;
  margin-top: 1.3rem;
}

.wigstationlogo div img {
  width: 25px;
  height: 25px;
  object-fit: cover;
}

.wigstationlogo div h1 {
  font-family: 'Sevillana', serif;
  font-weight: 700;
  letter-spacing: initial;
  font-size: 30px;
}


.menu-div .talktosalebtn .hires {
  margin-top: 30px;
  background-color: purple;
  color: white;
  padding: 13px 30px;
  font-size: 0.9em;
  text-align: center;
  border-radius: 5px;
  border-width: 0;
  opacity: 0.9;
  cursor: pointer;
  text-decoration: none;
  outline: none;
  font-family: 'Playfair Display', sans-serif;
}

.menu-div .talktosalebtn .hires:active {
  background-color: #3D5A5B;
  opacity: 0.9;
  cursor: pointer;
}

.menu-div .hires a {
  text-align: center;
  text-decoration: none;
  font-size: 15px;
  cursor: pointer;
  color: #fff;
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
}

.navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  padding-left: 12px;
  padding-right: 12px;
  max-width: 100rem;
  transition: top 0.3s ease;
  }