@media screen and (max-width: 1215px) {
    .Home{
        display: grid;
        place-items: center;
        padding-top: 10rem;
        background-color: #fdecefcf;
    }
}


@media screen and (max-width: 1200px) {
    .Home{
        display: grid;
        place-items: center;
        padding-top: 11rem;
        background-color: #fdecefcf;
    }
}

@media screen and (max-width:1160px) {
    .Home{
        display: grid;
        place-items: center;
        padding-top: 12rem;
        background-color: #fdecefcf;
        gap: 50px;
    }

    .content-section .content-container {
        display: flex;
        flex-direction: column;
        row-gap: 20px;
        padding-left: 4rem;
    }

    .image-container {
        position: relative;
        display: inline-block;
        padding-right: 4rem;
        margin-top: -75px;
    }
}

@media screen and (max-width: 990px) {
    .Homesubcontainer {
        width: 100%;
        display: grid;
        place-items: center;
        background-color: #fdecefcf;
      
      }
      
      .Homesubcontainer .Home-sub {
        display: grid;
        justify-content: center;
        align-items: center;
        grid-template-columns: 1fr;
        max-width: 100rem;
        width: 100%;
        row-gap: 10rem;
        padding-bottom: 0px;
      }
    

      .content-section{
        padding-left: 15px;
        padding-right: 15px;
      }
      
 
      .content-section .content-container {
        padding-left: -0rem;
        display: flex;
        justify-content: center;
        flex-direction: column;
        gap: 34px;    }


  .Home {
    display: grid;
    place-items: center;
    padding-top: 7rem;
    background-color: #fdecefcf;
  
}

.container {
    position: relative;
    left: 0;
    right: 0;
    margin: auto;
}

.image-container {
    position: relative;
    display: inline-block;
    padding-right: 0rem;
    margin-top: -75px;
}



}

@media screen and (max-width: 500px) {
    .stationary-bg{
        background-color: #f6d317eb;
        border-radius: 100px;
        text-align: center;
        padding-top: 15px;
        padding-bottom: 15px;
        padding-left: 15px;
        padding-right: 15px;
        display: flex;
        font-family: 'Playfair Display', sans-serif;
        flex-direction: column;
      }
}
