.image-carousel-container {
    width: 100%;
    padding-top: 3.4rem;
}

.carousel-slide {
    position: relative;
    width: 100%;
    outline: none;
}

.carousel-image {
    width: 100%;
    height: 100vh;
    /* Adjust the height to your desired value, e.g., 60% of the viewport height */
    object-fit: cover;
    object-position: center;
}

.carouseltextcon {
    position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        text-align: center;
        max-width: 400px;
        width: 100%;
        background-color: #40404045;
        border-radius: 5px;
}

.textcon {
color: white;
}

.textcon .text{
    display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;

}

.textcon .text h2{
    font-size: 30px;
        font-family: 'Merriweather', serif;
        font-weight: 500;
        padding-top: 1.3rem;
        text-transform: capitalize;
}

.textcon .text p{
    font-size: 15px;
        letter-spacing: 0.5px;
        font-weight: 400;
        text-decoration: none;
        line-height: 1.7em;
        width: 100%;
        max-width: 500px;
        font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
        padding-top: 1.5rem;
        text-transform: capitalize;
}

.textcon div{
    text-align: center;
    padding-top: 1.5rem;
    padding-bottom: 1.5rem;
}

.textcon div button{
    background-color: purple;
        color: white;
        padding: 13px 30px;
        font-size: 0.9em;
        text-align: center;
        border-radius: 5px;
        border-width: 0;
        opacity: 0.9;
        cursor: pointer;
        text-decoration: none;
        outline: none;
        font-family: 'Playfair Display', sans-serif;
}

.textcon div button:active {
    background-color: #3D5A5B;
    opacity: 0.9;
    cursor: pointer;
}

/* Optional: Adjust the number of slides to show based on screen width */
@media (min-width: 768px) {
    .carousel-slide {
        width: 50%;
    }

    .carousel-image {
        width: 100%;
        height: 70vh;
        object-fit: cover;
        object-position: center;
    }

    .carouseltextcon {
        width: 70%;
        /* Adjust the width of the text container */
    }
}

@media (min-width: 1200px) {
    .carousel-slide {
        width: 33.33%;
    }
}